import request from "@/utils/request";

export function login(data) {
  // 
  return request({
    url: "login",
    method: "post",
    data,
  });
}

export function getUserInfo() {
  // 
  return request({
    url: "profile",
    method: "get",
  });
}

export function getUser(id) {
  // 
  return request({
    url: `users/${id}`,
    method: "get",
  });
}

export function getUserList(params) {
  // 
  return request({
    url: "users",
    method: "get",
    params,
  });
}

export function createUser(data) {
  // 
  return request({
    url: "register",
    method: "post",
    data,
  });
}

export function updateUser(id, data) {
  // 
  return request({
    url: `users/${id}`,
    method: "patch",
    data,
  });
}

export function updateProfile(params) {
  // 
  return request({
    url: "users",
    method: "get",
    params,
  });
}

export function deleteUser(id) {
  // 
  return request({
    url: `users/${id}`,
    method: "delete",
  });
}
